<router-link class="rounded overflow-hidden bg-white shadow d-block position-relative" :to="{
    name: 'attractionInfo',
    params: {
      attractionId: attraction.ScenicSpotID
    }
  }" :title="`察看${attraction.ScenicSpotName}`"><img class="position-absolute link" src="@/assets/icons/link_icon.svg" alt="link icon"><img class="object-cover w-100" v-if="'PictureUrl1' in attraction.Picture" :src="attraction.Picture.PictureUrl1" :alt="attraction.Picture.PictureDescription1" height="163"><img class="object-cover w-100" v-else src="@/assets/images/illustration.png" alt="該資料沒有圖片" title="該資料沒有圖片" height="163"><div class="px-3 py-2"><h4 class="text-dark">{{ attraction.ScenicSpotName }}</h4><div class="d-flex"><div class="location me-3" :title="attraction.City" v-if="!!attraction.City">{{ attraction.City }}</div><div class="open-time text-truncate" :title="attraction.OpenTime" v-if="!!attraction.OpenTime">{{ attraction.OpenTime }}</div></div></div></router-link>